body {
  background-color: black;
}

.App {
  max-width: 50em;
  margin: 0 auto;
  padding: 30px;
}

.todo-input {
  font-size: 24px;
  border: 5px solid #00ff2b;
  border-radius: 5px;
  width: 100%;
}

.todo-input:focus {
  outline: none;
}

.done {
  text-decoration: line-through;
}

.delete {
  margin-left: auto;
  font-size: 16px;
  cursor: pointer;
  color: yellow;
}

.delete:hover {
  opacity: 0.7;
}

.todo {
  margin: 32px 0px;
  display: flex;
  align-items: center;
  font-size: 24px;
  color: white;
}

h1 {
  color: white;
}

.checkbox {
  width: 24px;
  height: 24px;
  border-radius: 50%;
  margin-right: 10px;
  cursor: pointer;
  background: white;
  font-size: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 2px solid red;
  transition: all 100ms ease-in-out;
  color: black;
}

.checkbox:hover {
  opacity: 0.7;
}